import React, { useState } from 'react';
import { Link, useLocation } from "react-router-dom";
import styles from './ViewItem.scss';
import classNames from 'classnames/bind';
import InPage from './../InPage/InPage';

const cx = classNames.bind(styles);

const axios = require('axios');

function ViewItem({children, image, subtitle, viewitem, viewid, viewpin}) {
  const location = useLocation();

  const [select, setSelect] = useState(false);
  const [first, setFirst] = useState(0);
  const [second, setSecond] = useState(0);
  const [third, setThird] = useState(0);
  const [fourth, setFourth] = useState(0);
  const [array, setArray] = useState([]);

  const [arrayTrue, setArrayTrue] = useState(false);
  const [apicheck, setApicheck] = useState(false);

  const onCheck = (e) => {
    console.log(e.target.name+"_"+e.target.id);
    console.log(e.target.id);
    if(e.target.name === 'select1') {
      setFirst(e.target.id);
    }
    else if(e.target.name === 'select2') {
      setSecond(e.target.id);
    }
    else if(e.target.name === 'select3') {
      setThird(e.target.id);
    }
    else if(e.target.name === 'select4'){
      setFourth(e.target.id);
    }
    else{
      alert("오류")
    }
  }

  const handleClick = () => {
    setSelect(true);
  }

  const handleBack = (e) => {
    e.preventDefault();

    if(first === 0 || second === 0 || third === 0 || fourth === 0){
      alert("다시 한번 확인해주세요.")
    } else{
      setSelect(false);

      axios.post('api/use/answer', {
        id: viewid,
        pin: viewpin,
        sub_name: viewitem,
        answer: [first, second, third, fourth]
      })
      .then(function (response) {
        console.log(response.body.check);
      })
      .catch(function (error) {
        console.log(error);
      });

      axios.post('api/use/sendelement', {
        id: viewid,
        pin: viewpin,
        sub_name: viewitem,
      })
      .then(function (response) {
        console.log(response.body.check);
        setApicheck(response.body.check);
      })
      .catch(function (error) {
        console.log(error);
      });
    }
  }

  return(
    <div>
      <div className={cx('viewitem-back')} onClick={handleClick}>
        <div className={cx('viewitem-title')}>{children}</div>
        <div>
          <img src={image}/>
        </div>
      </div>
      { (select === true && apicheck === false) &&
        <div>
          <div className={cx('viewitem-opacity')}></div>
          <div className={cx('inpage')}>
            <InPage onBack={handleBack} value={children} image={image} subtitle={subtitle} onCheck={onCheck }/>
          </div>
        </div>
      }
    </div>
  );
}

export default ViewItem;
